#contactpage{
    min-height: calc(100vh-4rem);
    width: 200;
    max-width: 65rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.contitle{
    font-size: 3rem;
    margin-bottom: 1rem;

}
.prodesc{
    font-weight: 300;
    font-size: 1rem;
}
.proimgs{
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;
}
.proimg{
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: auto;
    padding: 0.5rem;
}
.projects{
    padding: 2rem;
}
.condesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;

}
.conform{
    margin:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name,.email,.msg{
    font-size: medium;
    width: 50rem;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    border: none;
    color: aliceblue;
    border-radius: 2rem;
    background-color: rgb(40,40,40);
}
.submitbtn{
    background: white;
    border: 0cap;
    border-radius: 1rem;
    padding: 0.5rem 2.5rem;
    margin: 2rem;
    font-size: medium;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}