#works{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 65vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    text-align: center;
    padding: 0 1rem;
}
.worktitle{
    margin: 1rem;
    font-size: 3rem;
}
.workdesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 55rem;
}
.workimgs{
    display: flex;
    flex-wrap: wrap;
    width: 100vw;
    align-items: center;
    justify-content: center;
    max-width: 65rem;
}
.workimg{
    object-fit: cover;
    height: 20rem;
    margin: 0.3rem;

}
.worksbtn{
    padding: 0.5rem 2.5rem;
    background-color: white;
    border-radius: 2rem;
    font-size: 1rem;
    border: none;
    margin: 3rem;
}
@media screen and (max-width:585px) {
    .workimg{
        height: 40px;
    }
    
}