.NavBar{
    height:5rem;
    width: 100vm;
    background-color: rgb(30, 30, 30);
    max-width: 85rem;
    margin: auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 0;

}
.logo{
    object-fit: cover;
    height: 2rem;
    width: 4rem;
    margin-top: 2%;
}
.desktopmenuitem{
    margin: 1rem;
    cursor: pointer;
}
.desktopmenuitem:hover{
    color:yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;

}
.desktopmenubtn{
    background:white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    margin-top: 1%;
}


.desktopmenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;

}
.active{
    color: yellow;
    padding-bottom: 0.5rem;
    
}
.navmenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    height: fit-content;
    max-width: 15rem;
    background-color: rgb(40,40,40);
    border-radius: 1rem;
}

.mobmenu{
    display: none;
    height: 2rem;
    object-fit: cover;

}
.listitem{
    color:aliceblue;
    padding: 0.5rem 3rem;
    margin: 1rem;
    background-color: rgb(30, 30, 30);
}