#skills{
    overflow: hidden;
    width: 200;
    max-width: 65rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;


}
.skilltitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skilldesc{
    font-weight: 300;
    font-size: 0.85rem;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillbars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;

}
.skillbar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background-color: rgb(50,50,50);

}
.skillbarimg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}  
.skillbartext{
    font-size: 1rem;
    font-weight: 200;
}
@media screen and (max-width:480){
    .shillbartext>p{
        font-size: 3vw;
    }
    .skillbarimg{
        height: 2.25rem;
        width:2.25rem;
    }
    .skillbartext{
        font-size: 5vm;
    }
}