#intro{
    height: calc(100vh-5rem);
    width: 200vh;
    max-width: 85rem;
    margin: 0 auto;
    overflow: hidden;

}

.bg{
    position: absolute;
    right: 10%;
    z-index: -1;
    object-fit: cover;
    height: 80vh;
    margin-top: -650px;

}

.introcontent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 2rem;
    font-weight: 100;
}
.introname{
    color: yellow;
}
.intropara{
    font-size: medium;
    font-weight: 300;
}
.btn{
    background: white;
    margin: 1rem 0;
    padding:1rem 2rem;
    border-radius: 5rem;

}
.btnImg{
    object-fit: cover;
    margin: 0 0.50rem;
    height: 1rem;
}
@media  screen and (max-width:840px) {
    .bg{
        right: -20px;
    }
    .introcontent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
    
}